@import url("https://fonts.googleapis.com/css?family=Codystar:300&display=swap");

.container {
  height: 62.9rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: black;
  background-image: url('data:image/svg+xml,%3Csvg width="42" height="44" viewBox="0 0 42 44" xmlns="http://www.w3.org/2000/svg"%3E%3Cg id="Page-1" fill="none" fill-rule="evenodd"%3E%3Cg id="brick-wall" fill="%239C92AC" fill-opacity="0.4"%3E%3Cpath d="M0 0h42v44H0V0zm1 1h40v20H1V1zM0 23h20v20H0V23zm22 0h20v20H22V23z"/%3E%3C/g%3E%3C/g%3E%3C/svg%3E');
}

.neons {
  margin-top: 5rem;
  text-align: center;
}

.neons h1 {
  color: #00fff2;
  font-size: 4rem;
  text-align: center;
  font-weight: bold;
  -webkit-animation: glow 2s ease-in-out infinite alternate;
  -moz-animation: glow 2s ease-in-out infinite alternate;
  animation: glow 2s ease-in-out infinite alternate;
  #reservbutton{
      color:#00fff2a9;
  }
}

@keyframes glow {
  from {
    color: #fff;
    text-shadow: 0 0 10px #f4c7ab, 0 0 20px #f4c7ab, 0 0 30px #f4c7ab,
      0 0 40px #f4c7ab, 0 0 50px #f4c7ab, 0 0 60px #f4c7ab, 0 0 70px #f4c7ab,
      0 0 90px #f4c7ab;
  }

  to {
    color: gray;
    text-shadow: 0 0 20px #00fff2, 0 0 30px #00fff2, 0 0 40px #00fff2,
      0 0 50px #00fff2, 0 0 60px #00fff2, 0 0 70px #00fff2, 0 0 80px #00fff2,
      0 1 90px #00fff2;
  }
}
@media screen and (max-width:639px) {
    .neons h1 {
        font-size: 2.5rem;
      }
}
@media screen and (max-width:1632px) {
    .neons {
        margin-top: 1rem;
        h1 {
        font-size: 2rem;
      }}
      .container{
          height: 60rem;
      }
}
